
import XButton from 'vux/src/components/x-button/index.vue'

import Swiper from "swiper";
export default {
components: {
XButton
},
data() {
return {};
},
mounted() {
var mySwiper = new Swiper(".swiper-container", {
loop: true,
autoplay: true,
pagination: {
el: ".swiper-pagination"
},
autoplay: {
delay: 2500,
disableOnInteraction: false
}
});
}
};



