import Vue from 'vue'
import Vuex from 'vuex';
Vue.use(Vuex);

const state={
  user:{},   // 用户信息
  token:'',
  isWxConfigReady:false,
  activityInfo:''
}

const mutations = {
  setUser(state,user){
    state.user = user;
  },
  setToken(state,e){
    state.token = e;
  },
  setActivityInfo(state,e){
    state.activityInfo = e;
  },

}

export default new Vuex.Store({
  state,
  mutations
})



